import { FC } from "react";
import { Link, useHistory } from "react-router-dom";

import { Button } from "@ya.praktikum/react-developer-burger-ui-components";

import styles from "./styles.module.css";

export const Page404: FC = () => {
  const history = useHistory();

  const handleClick = () => {
    history.goBack();
  };

  return (
    <div className={styles.page}>
      <h3 className="text text_type_main-medium mb-6">404</h3>
      <h3 className="text text_type_main-medium mb-10">  </h3>
      <Button onClick={handleClick}> </Button>
      <div className={`${styles.links} mt-6`}>
        <p className="text text_type_main-default mr-2"> </p>
        <Link to="/" className={`${styles.link} text text_type_main-default`}>
           
        </Link>
      </div>
    </div>
  );
};
